    text-overflow: ellipsis; }
  .mm--home:after {
    left: var(--mm-item-indent); }

.mm ul {
  -webkit-overflow-scrolling: touch;
  position: fixed;
  top: calc(var(--mm-navbar-height) + 4rem);
  bottom: 0;
  left: 100%;
  width: 0%;
